@font-face {
  font-family: Stardew Valley Stonks;
  src: url("Stardew Valley Stonks.822eb553.otf");
  font-display: swap;
}

.label-hidden:after, .label-text:after, dt:after {
  content: ":";
  font-weight: 600;
}

.ok, .cancel, .edit, [role="tab"], .back, .column-sort > button, .collapse-arrow {
  box-shadow: none;
  color: #000;
  background: none;
  border: none;
  outline: none;
  padding: 0;
}

.button, .file-input > div:first-child > label {
  color: #000;
  image-rendering: pixelated;
  background: none;
  border-image-source: url("Button.0b5d963a.png");
  border-image-slice: 14 fill;
  border-image-width: .25em;
  padding: .625em .5em .5em;
}

#app-error, dialog, [role="tab"], [role="tabpanel"], #chart-tooltip, #settings-load\/save > section:first-child > ul, footer > span, body > span, .skill {
  image-rendering: pixelated;
  border-image-source: url("Border Box.1df488aa.png");
  border-image-slice: 16 fill;
  border-image-width: 1rem;
  border-image-outset: .75rem;
  margin: .75rem;
}

.skill {
  border-image-outset: 0;
  margin: 0;
  padding: .75rem;
}

#app-error > *, dialog > *, [role="tab"] > *, [role="tabpanel"] > *, #chart-tooltip > *, #settings-load\/save > section:first-child > ul > *, footer > span > *, body > span > *, .skill > * {
  image-rendering: initial;
}

.checkbox > input:focus + img, .select:focus-within, .file-input > div:first-child > label:focus-within, .column-sort > button:focus, .collapse-arrow:focus:before, .input-items > summary:focus:before, a:focus, :focus {
  outline: var(--border-thin) solid #d83700;
  outline-offset: var(--border-thin);
}

input[type="range"]:hover, [role="tab"]:hover, .input-box:hover, .button:hover, .file-input > div:first-child > label:hover {
  filter: brightness(1.15);
}

.ok:hover, .cancel:hover, .edit:hover, details > summary:hover:before, .checkbox:hover > input + img, .back:hover:before, .collapse-arrow:hover:before {
  transform: scale(1.1);
}

:root {
  word-spacing: .125em;
  --border-thin: 2px;
  --border-med: 3px;
  font-family: Stardew Valley Stonks, Nunito, sans;
  line-height: 1.25;
}

body {
  image-rendering: pixelated;
  background-image: url("Background.f7f826e0.png");
  background-position: bottom;
  background-size: cover;
  background-attachment: fixed;
  flex-direction: column;
  align-items: center;
  width: 100%;
  min-width: min-content;
  min-height: 100vh;
  margin: 0;
  display: flex;
  overflow-y: scroll;
}

body > span {
  width: max-content;
  margin: 1em auto;
  padding: 1em;
  font-size: 1.5rem;
  display: block;
}

body > * {
  box-sizing: border-box;
  image-rendering: initial;
  width: 100%;
}

header {
  max-width: 100vw;
  padding: 1.5em;
}

header > a {
  flex-wrap: wrap;
  justify-content: center;
  gap: 1em 2em;
  display: flex;
}

header > a > div {
  align-items: center;
  display: flex;
}

footer {
  text-align: center;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  gap: 1em;
  min-height: 15vh;
  padding: 1em;
  font-size: 1.25rem;
  display: flex;
}

footer > span {
  padding: .625em .75em .5em;
}

@media (width <= calc(3em + 428px)) {
  header > a > div {
    aspect-ratio: 428 / 84;
    justify-content: center;
    width: 100%;
  }
}

img {
  -webkit-user-select: none;
  user-select: none;
}

a {
  color: #004d92;
}

a:visited {
  color: #8300bf;
}

dl {
  grid-template-columns: fit-content(13em) auto;
  gap: .5em;
  margin: 0;
  display: grid;
}

dt {
  text-align: right;
}

dd, h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
}

.file-input > div:first-child > label > input, .select-control > input[inputmode="none"], .checkbox > input {
  opacity: 0;
  position: absolute;
  transform: scale(0);
}

tr:nth-child(odd), #settings-load\/save > section:first-child > ul > li:nth-child(odd) {
  background-color: #f9ba66;
}

tr:nth-child(2n), #settings-load\/save > section:first-child > ul > li:nth-child(2n) {
  background-color: #ffd284;
}

.collapse-arrow:before, .column-sort > button:after, .back:before, details > summary:before {
  content: "";
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 1em;
  height: 1em;
  display: inline-block;
}

.back:before {
  background-image: url("Arrow Left.fcf0b818.png");
}

.collapse-arrow[aria-pressed="false"]:before, details > summary:before {
  background-image: url("Arrow Right.1e24a600.png");
}

.column-sort:not([aria-sort]) > button:hover:after, .column-sort[aria-sort="ascending"] > button:after {
  background-image: url("Arrow Up.4db4c69c.png");
}

.collapse-arrow[aria-pressed="true"]:before, .column-sort[aria-sort="descending"] > button:after, details[open] > summary:before {
  background-image: url("Arrow Down.a44a5e4e.png");
}

.winter, .fall, .summer, .spring {
  height: 1.25em;
}

.spring {
  content: url("Spring.0dd8677e.png");
}

.summer {
  content: url("Summer.f7c128ff.png");
}

.fall {
  content: url("Fall.24052abc.png");
}

.winter {
  content: url("Winter.061496b2.png");
}

.icon, .quality, .error, .warning, .random, .special-charm, .bears-knowledge, .botanist, .gatherer, .agriculturist, .artisan, .tiller, .juice, .wine, .pickles, .jam {
  width: 1.5em;
  height: 1.5em;
}

.icon-processor {
  width: 1.25em;
}

.icon-text {
  width: max-content;
  display: inline-block;
}

.icon-text > :first-child {
  vertical-align: text-bottom;
  margin-right: .375em;
}

.jam {
  content: url("Jam.83da7ced.png");
}

.pickles {
  content: url("Pickles.9a6602c8.png");
}

.wine {
  content: url("Wine.66836bc8.png");
}

.juice {
  content: url("Juice.c39b1ec8.png");
}

.tiller {
  content: url("Tiller.22359b91.png");
}

.artisan {
  content: url("Artisan.756110e4.png");
}

.agriculturist {
  content: url("Agriculturist.460d5f11.png");
}

.gatherer {
  content: url("Gatherer.150795ae.png");
}

.botanist {
  content: url("Botanist.baffb1b7.png");
}

.foraging, .farming {
  width: 1.25em;
  height: 1.25em;
}

.farming {
  content: url("Farming.36b3cdcd.png");
}

.foraging {
  content: url("Foraging.ed9a543f.png");
}

.iridium {
  --color: #c272de;
  --img: url("Iridium.6977bc45.png");
}

.gold {
  --color: #f7e412;
  --img: url("Gold.12cd3298.png");
}

.silver {
  --color: #bdc0c3;
  --img: url("Silver.551e04a1.png");
}

.normal {
  --color: white;
  --img: url("Normal.2db4444f.png");
}

.edit, .cancel, .ok {
  background-size: contain;
}

.ok {
  background-image: url("Ok.5c438721.png");
  width: 2.5rem;
  height: 2.5rem;
}

.cancel {
  background-image: url("Cancel.cad74dc6.png");
  width: 2.5rem;
  height: 2.5rem;
}

.edit {
  background-image: url("Edit.2c68efdd.png");
  width: 1.5rem;
  height: 1.5rem;
}

.bears-knowledge {
  content: url("Bears Knowledge.a40b821e.png");
}

.special-charm {
  content: url("Special Charm.8675ca04.png");
}

.random {
  content: url("Random.a117370d.png");
}

.warning {
  content: url("Warning.1689f328.png");
}

.error {
  content: url("Error.d27bec23.png");
}

html {
  scroll-snap-type: y mandatory;
}

header {
  scroll-snap-align: start;
}

main {
  scroll-snap-align: start;
  grid-template-columns: minmax(37.5rem, 1fr) minmax(42.5rem, 1fr);
  gap: 1rem;
  height: 100vh;
  padding: 1rem;
  display: grid;
}

main > section {
  grid-template-rows: max-content;
  display: grid;
  overflow: hidden;
}

footer {
  scroll-snap-align: end;
}

#app-error {
  flex-direction: column;
  grid-column: span 2;
  align-items: center;
  row-gap: 1em;
  padding: 2em 1em 1em;
  display: flex;
}

#app-error > span > .icon-message {
  align-items: center;
  font-size: 1.25rem;
}

ul {
  padding: 0;
  list-style: none;
}

dl {
  font-size: 1.125rem;
}

dialog {
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  row-gap: 1em;
  min-width: 15rem;
  max-width: min(60ch, 40vw);
  min-height: 10rem;
  margin: auto;
  padding: 1em;
  display: flex;
}

dialog > h1 {
  text-align: center;
  font-size: 1.5rem;
  font-weight: normal;
}

dialog > div {
  justify-content: center;
  align-items: center;
  column-gap: 1em;
  width: 100%;
  display: flex;
}

dialog > div:last-child {
  justify-content: space-between;
}

input, button {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  word-spacing: inherit;
}

button {
  cursor: pointer;
}

input[type="range"] {
  -webkit-appearance: none;
  cursor: pointer;
  height: calc(var(--border-thin) * 6 * 1.5);
  background: none;
  width: max(100px, 10rem);
  margin: 0;
}

input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  box-shadow: none;
  width: calc(var(--border-thin) * 10 * 1.5);
  height: calc(var(--border-thin) * 6 * 1.5);
  background: url("Range Thumb.97c73dca.png") 0 0 / contain;
  border: none;
  border-radius: 0;
}

input[type="range"]::-moz-range-thumb {
  box-shadow: none;
  width: calc(var(--border-thin) * 10 * 1.5);
  height: calc(var(--border-thin) * 6 * 1.5);
  background: url("Range Thumb.97c73dca.png") 0 0 / contain;
  border: none;
  border-radius: 0;
}

input[type="range"]::-webkit-slider-runnable-track {
  height: calc(var(--border-thin) * 6 * 1.5);
  border-image-source: url("Range Track.fd64cd9c.png");
  border-image-slice: 22 fill;
  border-image-width: calc(var(--border-thin) * 2 * 1.5);
}

input[type="range"]::-moz-range-track {
  height: calc(var(--border-thin) * 6 * 1.5);
  border-image-source: url("Range Track.fd64cd9c.png");
  border-image-slice: 22 fill;
  border-image-width: calc(var(--border-thin) * 2 * 1.5);
}

details > summary {
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
  border: #5b2b2a solid var(--border-thin);
  background-color: #ffd284;
  align-items: center;
  padding: .5em .5em .375em;
  font-size: 1.25rem;
  list-style: none;
  display: flex;
}

details > summary::marker {
  display: none;
}

details > summary::-webkit-details-marker {
  display: none;
}

details > summary:before {
  margin: 0 .5em .25em 0;
}

details > div {
  flex-direction: column;
  row-gap: .5em;
  margin-block: 1em;
  display: flex;
}

[role="tablist"] {
  column-gap: .5em;
  padding-inline: .75rem;
  display: flex;
}

[role="tab"] {
  outline-offset: 0;
  border-image-width: 1rem 1rem 0;
  border-image-outset: .75rem .75rem 0;
  min-width: 5em;
  margin-bottom: 0;
  padding: .5em .625em .375em;
  font-size: 1.5rem;
}

[role="tab"][aria-selected="true"] {
  z-index: 1;
  transform: translateY(.5rem);
}

[role="tabpanel"] {
  overscroll-behavior: contain;
  overflow: auto;
}

[role="tabpanel"] > div {
  box-sizing: border-box;
  flex-direction: column;
  row-gap: 1em;
  height: 100%;
  padding: 1.5em;
  display: flex;
}

@media (width <= 83rem) {
  html {
    scroll-snap-type: none;
  }

  main {
    height: unset;
    grid-template-columns: 1fr;
    min-width: min-content;
  }

  main > section {
    overflow: unset;
    min-height: max(42.5rem, 50vh - 1.5rem);
  }

  [role="tabpanel"] {
    overflow: unset;
  }

  .visualization-chart {
    height: max(60rem, 50vh - 1.5rem);
  }

  .visualization-chart > [role="tabpanel"] {
    overflow: hidden;
  }
}

@media (width <= 44.5rem) {
  :root {
    font-size: clamp(75%, 2vw, 100%);
  }
}

.hidden-text, .label-hidden {
  -webkit-user-select: none;
  user-select: none;
  font-size: 0;
  position: absolute;
}

.label-text {
  margin-right: .5em;
}

.input-box {
  image-rendering: pixelated;
  -moz-appearance: textfield;
  background: none;
  border-image-source: url("Input Box.58079c06.png");
  border-image-slice: 18 18 12 12 fill;
  border-image-width: .5rem;
  padding: .625em .5em .5em;
}

.input-box::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.input-box::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.checkbox {
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
  position: relative;
}

.checkbox > input {
  top: 0;
  left: 0;
}

.checkbox > input + img {
  content: url("Checkbox.76be4d9e.png");
  vertical-align: text-bottom;
  width: 1.5em;
  height: 1.5em;
}

.checkbox > input + img:not(:last-child) {
  margin-right: .5em;
}

.checkbox > input:checked + img {
  content: url("Checkedbox.8bb4327d.png");
}

.checkbox > input:focus + img {
  outline-offset: 0;
}

.select {
  --border: var(--border-med) solid #5b2b2a;
  cursor: pointer;
  display: inline-flex;
}

.select[aria-expanded="true"]:focus-within {
  outline: none;
}

.select[aria-expanded="false"]:hover > div > .select-control {
  background-color: #ffd284;
}

.select > div {
  position: relative;
}

.select > img {
  content: url("Select Spinner.a18c9517.png");
  border: var(--border);
  border-left: none;
  height: 2rem;
}

.select-control {
  box-sizing: border-box;
  border: var(--border);
  background-color: #f9ba66;
  align-items: center;
  height: 100%;
  padding-inline: .5rem;
  display: flex;
  position: relative;
}

.select-control > input[type="search"] {
  background: none;
  border: none;
  outline: none;
  width: calc(100% - 1rem);
  height: 100%;
  padding: 0;
  position: absolute;
}

.select-control > input[type="search"]:not(:placeholder-shown) + * {
  visibility: hidden;
}

.select-control > span {
  margin-top: .125rem;
}

.select-control span {
  text-overflow: ellipsis;
  white-space: nowrap;
  display: inline;
  overflow: hidden;
}

.select-list {
  z-index: 2;
  border: var(--border);
  box-sizing: border-box;
  background-color: #f9ba66;
  border-top: none;
  width: 100%;
  max-height: 50vh;
  position: absolute;
  top: 100%;
  left: 0;
  overflow: auto;
}

.select-list > li {
  padding: .5rem;
}

.select-list > li.empty {
  text-align: center;
  padding: .75rem;
}

.select-list > li[aria-selected="true"] {
  background-color: #ffd284;
}

.preset {
  justify-content: space-between;
  align-items: baseline;
  column-gap: 1em;
  display: flex;
}

.preset > span:first-child {
  font-size: 1.25rem;
}

.file-input {
  display: block;
}

.file-input > div:first-child {
  align-items: center;
  column-gap: 1.5em;
  display: flex;
}

.file-input > div:first-child > label {
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
  margin-bottom: .5em;
  display: inline-block;
}

.file-input > div:last-child {
  border: var(--border-thin) solid #5b2b2a;
  background: #ffd284;
  justify-content: center;
  align-items: center;
  height: 10rem;
  display: flex;
}

.messages {
  flex-direction: column;
  align-items: flex-start;
  row-gap: .5em;
  display: flex;
}

.messages:empty {
  display: none;
}

.messages-large {
  margin: 25% auto auto;
  font-size: 1.5rem;
}

.icon-message {
  column-gap: .5em;
  display: flex;
}

.icon-message > :last-child {
  flex-direction: column;
  row-gap: .25em;
  margin-top: .375em;
  display: flex;
}

.icon-message > :last-child > ul {
  list-style: unset;
  padding-left: 1em;
}

.settings-columns {
  flex-wrap: wrap;
  gap: .5em 1em;
  display: flex;
}

.settings-column {
  flex-direction: column;
  row-gap: .5em;
  margin-right: auto;
  display: flex;
}

#skills-details {
  flex-direction: column;
  align-items: center;
  row-gap: 1.5em;
  display: flex;
}

#skills-details > div:first-child {
  flex-wrap: wrap;
  justify-content: space-around;
  gap: 1em;
  display: flex;
}

.skill {
  border-image-width: .75rem;
  flex-direction: column;
  align-items: center;
  row-gap: 1em;
  padding: calc(.75rem + .75em);
  display: flex;
}

.skill > span:first-child {
  font-size: 1.5rem;
}

.skill-level {
  flex-wrap: wrap;
  gap: 1em;
  display: flex;
}

.professions {
  grid-template-columns: repeat(2, 1fr);
  gap: 1em;
  width: max-content;
  display: grid;
}

.professions > div {
  align-items: center;
  display: flex;
}

.professions > div:first-child, .professions > div:last-child:nth-child(2) {
  grid-row: span 2;
}

.number-and-range {
  align-items: center;
  column-gap: .5em;
  display: flex;
}

.number-and-range > label {
  align-items: center;
  display: flex;
}

.seasons {
  grid-template-columns: repeat(4, 1fr);
  display: grid;
}

.calendar {
  flex-direction: column;
  align-items: center;
  row-gap: 2em;
  display: flex;
}

.calendar-season {
  padding: var(--border-thin);
  min-width: min-content;
}

.calendar-header {
  grid-template-columns: repeat(3, 1fr);
  place-items: center;
  column-gap: 1em;
  padding: 1em;
  display: grid;
}

.calendar-header > span:last-child {
  grid-column: 3;
  font-size: 1.25rem;
}

.calendar-header > span:first-child {
  grid-column: 2;
  font-size: 2rem;
}

.calendar-days {
  gap: var(--border-thin);
  grid-template-columns: repeat(7, minmax(4rem, 7rem));
  grid-auto-rows: 1fr;
  display: grid;
}

.calendar-days > div {
  aspect-ratio: 1;
  outline: var(--border-thin) solid #5b2b2a;
  background-color: #ffd284;
  justify-content: center;
  align-items: center;
  padding: 10%;
  display: flex;
  position: relative;
}

.calendar-days > div > img {
  height: unset;
  width: 50%;
}

.calendar-days > div:nth-child(28n+1) {
  counter-reset: day;
}

.calendar-days > div:before {
  counter-increment: day;
  content: counter(day);
  position: absolute;
  top: 5%;
  left: calc(5% + .125rem);
}

#chart {
  flex-grow: 1;
  min-height: 0;
  margin-inline: -1em;
  overflow: hidden;
}

#chart-controls {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 1em 2em;
  display: flex;
}

#chart-controls > div, #chart-controls > div > div {
  flex-wrap: wrap;
  align-items: center;
  gap: 1em .5em;
  display: flex;
}

#chart-tooltip {
  flex-direction: column;
  align-items: center;
  row-gap: .5em;
  padding: 1em;
  display: flex;
}

#chart-tooltip > h1 {
  font-size: 1.25rem;
  font-weight: normal;
}

#chart-tooltip > span {
  font-size: 1.125rem;
}

.recharts-tooltip-wrapper:focus {
  outline: none;
}

.recharts-bar-rectangle {
  cursor: pointer;
}

.recharts-bar-rectangle > path, .recharts-bar-rectangle > .recharts-active-bar > path {
  fill: #0060b6;
}

.recharts-cartesian-axis-tick-value {
  fill: #000;
  -webkit-user-select: none;
  user-select: none;
}

.recharts-cartesian-axis-line, .recharts-cartesian-axis-tick-line {
  stroke: #000;
  stroke-width: 1.5px;
}

.recharts-bar-rectangles > path, .recharts-active-bar > path {
  cursor: pointer;
  fill: #0000;
}

.recharts-brush > rect:first-child {
  fill: #ffd284;
  stroke: #5b2b2a;
  stroke-opacity: .666667;
  stroke-width: var(--border-thin);
}

.recharts-brush-slide {
  fill-opacity: .666667;
  fill: #5b2b2a;
}

.recharts-brush-traveller > rect:first-child {
  fill: #5b2b2a;
